<template>
    <v-container :fluid="true">
      <div class="text-center">
          <h2>Examenes de Patología con Info. Pendiente</h2>
          <h3>Fecha: {{FechaActual()}}</h3>
      </div>
        <v-form  >
            <v-row class="ma-0 pa-0" justify="center" >              
              <v-col cols="12" sm="6" md="4 mb-2" class="ma-0 pa-0" >
                <v-card  outlined  class="blue lighten-3 pt-3 mx-1 px-1"  v-if="ambienteDeModificacion==true">
                  <v-row class="ma-0 pa-0">
                <v-col class="ma-0 pa-0" cols="6">
                <v-switch  dense  v-model="examenesRealizadosPat" :label="examenesPat.muestra" :value="examenesPat.id" class="ma-0 pa-0" :color="color_salud"  >  </v-switch>
              </v-col>
              <v-col class="ma-0 pa-0" cols="5">
                 <v-text-field class="ma-0 pa-0" v-model="examenesPat.cantidad" type="number"  :color="color_salud"  label="Cantidad" dense outlined :background-color="color_lighten_salud"  ></v-text-field>
              </v-col>  
               </v-row>           
                  <v-row v-if="verificarExamenRealizado(examenesPat.id)" justify="center" class="ma-0 pa-0">
                    <v-row class="ma-0 pa-0">
                        <v-col  cols="5" class="ma-0 pa-0">
                        <v-checkbox
                          class="ma-0 pa-0"  
                        dense
                        v-model="examenesPat.enviado"
                        label="¿Enviado?"
                        :color="color_salud" 
                        :value="!enviado"
                        hide-details
                      ></v-checkbox>
                      </v-col>
                      <v-col  cols="7" class="ma-0 pa-0">
                          <v-text-field v-model="examenesPat.fechaEnvio" type="date"  :color="color_salud" label="Fecha de Envío" dense  outlined :background-color="color_lighten_salud"  ></v-text-field>
                      </v-col>
                     </v-row>
                     <v-row  class="ma-0 pa-0">
                      <v-col cols="5" class="ma-0 pa-0">
                      <v-checkbox
                        class="ma-0 pa-0"  
                        dense
                        v-model="examenesPat.pagado"
                        label="¿Pagado?"
                        :color="color_salud"
                        :value="!pagado"
                        hide-details
                      ></v-checkbox>
                      </v-col>
                      <v-col cols="7" class="ma-0 pa-0">
                        <v-text-field v-model="examenesPat.fechaResultado" type="date"  :color="color_salud" label="Entrega del Resultado" dense  outlined :background-color="color_lighten_salud"  ></v-text-field>
                      </v-col>
                    </v-row>
                    </v-row>                   
                </v-card>
              </v-col>
            </v-row>
  <v-row justify="center" class="ma-0 pa-0"  v-if="ambienteDeModificacion==true">

     <v-col  cols="11" md ="4">
     <v-btn class="px-5" block dark :color="color_salud" @click="Modificar(examenesPat)" >Modificar</v-btn>
    </v-col>
        <v-col cols="11"  md ="4">
     <v-btn class="px-5" block dark  @click="ambienteDeModificacion=false" >Cancelar</v-btn>
    </v-col>
  </v-row>
  <v-row justify="center" class="ma-0 pa-0">
      <v-alert v-if="mensajeError!==''" dense outlined type="error">
          {{ mensajeError }}</v-alert>
           <v-alert v-if="modificacionHecha" dense class="text-center" type="success">
          Registro Exitoso</v-alert>
  </v-row>
  </v-form>
  <v-row>
     <v-col   class="mx-auto">
      <tabla-pendiente @DatosParaModificarExamenesPendiente="LlenaDatosParaModificacion"  @CancelarEditar="CancelarEdicion" ref="tabla" ></tabla-pendiente>
      </v-col>
  </v-row>
    </v-container>
</template>
<script>
import  {mapState} from 'vuex'
import TablaPendiente from '../components/TablaPatologiaPendiente'
export default {
    name:"PatologiaPendiente",
    components:{TablaPendiente},
    data() {
        return {
          color_salud:'blue darken-3',
          color_lighten_salud:'blue lighten-5',
          color_text_salud:'blue--text text-lighten-4',
          mensajeError:'',
           modificacionHecha:false,
          ambienteDeModificacion:false,
          examen:'',        
          //contenedor de temporal de datos que seran eliminados
          //esta variable contiene a todos los que estana ctivos o examenes realizados switch True 
          examenesRealizadosPat:[],
          enviado:true,
          pagado:true,
          dataTipoMuestraPat:[],
          dataMuestraPat:[],
          
          //esta variable contiene TODO LOS DATOS de los examenes que seran registrados omodificados
        examenesPat:[],
      }
    },
    computed:{
        ...mapState(['token'])
    },
    methods: {
     CancelarEdicion(){
         this.ambienteDeModificacion =false;
     },
      FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
      convertidorFecha(str){
          if(str==null){
            return 'Sin Data'
          }else{
            const f = new Date(str);
              var año=f.getFullYear()
              var mes=(f.getMonth() + 1)
                  mes=(mes<10)?('0'+mes):mes;
              var dia=f.getDate()
                  dia=(dia<10)?('0'+dia):dia;
          return año+'-'+mes+'-'+dia;
          }
     },
      eliminarExamen(e){  

     },
      LlenaDatosParaModificacion(d){
        this.ambienteDeModificacion=true;
        //Vaciamos par modificar otro dato
        this.examenesRealizadosPat=[];     
            if (!(d.fechaAtencion==null)){
                   //agregando a los examenes realizados si es que tien fecha de atendido
                      this.examenesRealizadosPat[0]=d.idMuestraPat
                 }  
                  this.examenesPat={
                  idExamen:d.idExamen,
                  id:d.idMuestraPat,
                  muestra:d.examen,
                  atendido:d.fechaAtencion==null? false:true ,
                  cantidad:d.cantidad,
                  pagado:d.estadoPago==0? false:true,
                  enviado:d.estadoEnvio==0? false:true ,
                  fechaEnvio:d.fechaEnvioMuestra==null? '':this.convertidorFecha(d.fechaEnvioMuestra),
                  fechaResultado:d.fechaEntregaResultado==null? '':this.convertidorFecha(d.fechaEntregaResultado)
                  }
        
              
      },
       verificarExamenRealizado(ele){
        
            var existe=this.examenesRealizadosPat.includes(ele);
              //como no ah sido atendido no le agregamos datos al card
            if(!existe){
                   this.examenesPat.atendido=false
                   this.examenesPat.fechaResultado=''
                   this.examenesPat.fechaEnvio=''
                    this.examenesPat.pagado=false
                     this.examenesPat.enviado=false
                   
                   }
                  else{
                      this.examenesPat.atendido=true
                   }

      return existe;
      },
      Modificar(d){
           var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };

          this.axios.post("/modificarUnSoloExamPatologia",{
                  idExamen:d.idExamen,
                  muestra:d.examen,
                  atendido:d.atendido? this.FechaActual():null ,
                  cantidad:d.cantidad,
                  pagado:d.pagado==0? false:true,
                  enviado:d.enviado==0? false:true,
                  fechaResultado:d.fechaResultado,
                  fechaEnvio:d.fechaEnvio
          },{headers})
         .then(res=> {
                this.modificacionHecha=true;
                this.$refs.tabla.ListarMuestras();
                this.ambienteDeModificacion =false;  
                this.examenesPat={}
                this.examenesRealizadosPat=[]
                  setTimeout(()=>{
                  this.modificacionHecha=false ;
                },2000)
              
                  
          })
          .catch(err=>{
             err.response.data.sqlMessage? this.mensajeError=err.response.data.sqlMessage :   this.mensajeError=err;
              setTimeout(()=>{  this.mensajeError=''},2000)
              console.log(err)
              })   
      }
    },
}
</script>
<style scoped>
.v-input__slot{
margin:0;
}
.v-input__control.v-messages.theme--light.red--text .text--darken-4{
  display: none !important;
}
.theme--light.v-application{
  background-color:#FFCDD2;
}

.theme--light.v-list{
  background: #FFCDD2;
}

::v-deep input::-webkit-outer-spin-button,
::v-deep input::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}

</style>